* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#game-container {
  grid-template-rows: 50px 200px 50px;
  grid-template-columns: 200px 100px;
  justify-content: center;
  margin: 100px auto auto;
  display: grid;
}

#setup-container {
  background-color: #39cd4a;
  border: 1px solid #000;
}

#name-container {
  text-align: center;
  background-color: #0ff;
  grid-area: 1 / 1 / 2 / 3;
  padding-top: 5px;
}

#img-container {
  background-color: #2c4f4f;
  grid-area: 2 / 1 / 3 / 2;
  justify-content: center;
  align-items: center;
  display: flex;
}

#img-container img {
  background-color: #97b5b5;
}

#btn-container {
  background-color: #00f;
  grid-area: 3 / 1 / 4 / 4;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

#stats-container {
  text-align: center;
  background-color: red;
  grid-area: 2 / 2 / 3 / 3;
  padding: 15px;
}

#stats-container h4 {
  padding: 2px;
}

#petImg {
  width: 400px;
  height: 200px;
  object-fit: cover;
}

